/* NewBooking.css */

.booking-container {
    max-width: 600px;
    margin: 0 ;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

  }
  
  .heading {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .customer-section,
  .hall-section,
  .date-time-section,
  .package-section,
  .package-details,
  .custom-package,
  .food-section,
  .soft-drink-section,
  .hard-drink-section {
    margin-bottom: 20px;
  }
  
  .label {
    display: block;
    margin-bottom: 5px;
  }
  
  .input,
  .select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .button,
  .submit-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 20px;
  }
  
  .button:hover,
  .submit-button:hover {
    background-color: #0056b3;
  }
  
  .checkbox-list {
    list-style: none;
    padding: 0;
  }
  
  .checkbox-item {
    margin-bottom: 10px;
  }
  
  .checkbox-label {
    display: inline-block;
    margin-left: 5px;
  }
  
  .checkbox {
    margin-right: 5px;
  }

  
  /* Styles for the customer table */
.customer-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.customer-table th,
.customer-table td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

.customer-table th {
  background-color: #f2f2f2;
}

.customer-table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}


