/* AdminMenu.css */

.admin-menu-container {
    max-width: 600px;
    margin: 0 ;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .admin-menu-heading {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .admin-menu-section {
    margin-bottom: 20px;
  }
  
  .admin-menu-label {
    display: block;
    margin-bottom: 5px;
  }
  
  .admin-menu-input,
  .admin-menu-select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .admin-menu-button,
  .admin-menu-submit-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .admin-menu-button:hover,
  .admin-menu-submit-button:hover {
    background-color: #0056b3;
  }
  
  .admin-menu-checkbox-list {
    list-style: none;
    padding: 0;
  }
  
  .admin-menu-checkbox-item {
    margin-bottom: 10px;
  }
  
  .admin-menu-checkbox-label {
    display: inline-block;
    margin-left: 5px;
  }
  
  .admin-menu-checkbox {
    margin-right: 5px;
  }
  
  /* Styles for the admin menu list */
  .admin-menu-list {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  .admin-menu-list th,
  .admin-menu-list td {
    padding: 8px;
    border-bottom: 1px solid #ddd;
    text-align: left;
  }
  
  .admin-menu-list th {
    background-color: #f2f2f2;
  }
  
  .admin-menu-list tbody tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .admin-menu-subheading {
    display: inline-block;
    padding: 7px 14px; /* Adjusted padding */
    margin: 0;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 15px; /* Adjusted font size */
  }
  
  
  
  .admin-menu-subheading:hover {
    background-color: #275b93;
  }

 /* Styles for delete button */
.admin-menu-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Align buttons to start of the column */
  }
  
  /* Styles for delete button */
.delete-button {
    background-color: rgb(203, 59, 59);
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    margin-left: 20px; /* Adjust margin as needed */
    margin-top: 5px; /* Add some space between buttons */
  }
  
  